@keyframes text_bottom2top {
  0% {
    transform: translateY(100%);
    opacity: 0; }

  100% {
    transform: translateY(0);
    opacity: 1; } }


@keyframes text_opacity {
  0% {
    opacity: 0; }

  100% {
    opacity: 1; } }


@keyframes tag_item {
  0% {
    opacity: 0;
    position: relative;
    top: 3rem; }

  100% {
    opacity: 1;
    position: relative;
    top: 0; } }


@keyframes arrow_anim {
  0% {
    transform: translateY(2rem);
    opacity: 0; }

  100% {
    transform: translateY(0);
    opacity: 1; } }


@keyframes intro_bg {
  0% {
    background: #fff; }

  100% {
    background: #0000ff; } }


@keyframes start_scale {
  0% {
    display: block;
    transform: scale(0); }

  30% {
    display: block;
    transform: scale(1); }

  99% {
    display: block;
    transform: scale(1); }

  100% {
    display: none;
    transform: scale(0); } }


@keyframes start_c1 {
  0% {
    margin-left: 0; }

  100% {
    margin-left: -5.6rem; } }


@keyframes start_c2 {
  0% {
    box-shadow: 0 0 0 0 #00f;
    transform: scale(1); }

  40% {
    box-shadow: 0 0 3rem 2rem #00f;
    transform: scale(1); }

  60% {
    box-shadow: 0 0 3rem 2rem #00f;
    transform: scale(1); }

  99% {
    box-shadow: 0 0 3rem 2rem #00f;
    transform: scale(18); }

  100% {
    display: none;
    box-shadow: 0 0 3rem 2rem #00f;
    transform: scale(18); } }


@media (max-width: 768px) {
  @keyframes start_c1 {
    0% {
      margin-left: 0; }

    100% {
      margin-left: -3rem; } }

  @keyframes start_c2 {
    0% {
      box-shadow: 0 0 0 0 #00f;
      transform: scale(1); }

    40% {
      box-shadow: 0 0 4rem 3rem #00f;
      transform: scale(1); }

    60% {
      box-shadow: 0 0 4rem 3rem #00f;
      transform: scale(1); }

    99% {
      box-shadow: 0 0 4rem 3rem #00f;
      transform: scale(18); }

    100% {
      display: none;
      box-shadow: 0 0 4rem 3rem #00f;
      transform: scale(18); } } }

@keyframes next_project {
  60% {
    transform: scale(1, 1);
    height: 100vh;
    min-height: 100vh;
    margin-top: 0;
    position: absolute; }

  100% {
    transform: scale(1, 1);
    height: 100vh;
    min-height: 100vh;
    margin-top: 0;
    position: absolute; } }


